body, html {  
  background: 
    -webkit-gradient(
      linear, 
      left top, left bottom, 
      from(rgba(255, 255, 255, 0.45)), 
      to(rgba(255, 255, 255, 0.45))
    ),
    url('/img/grey-asoggetti.jpg') no-repeat center center fixed;  
  background: 
    -webkit-linear-gradient(
      rgba(255, 255, 255, 0.45), 
      rgba(255, 255, 255, 0.45)
    ),
    url('/img/grey-asoggetti.jpg') no-repeat center center fixed;  
  background: 
    -o-linear-gradient(
      rgba(255, 255, 255, 0.45), 
      rgba(255, 255, 255, 0.45)
    ),
    url('/img/grey-asoggetti.jpg') no-repeat center center fixed;  
  background: 
    linear-gradient(
      rgba(255, 255, 255, 0.45), 
      rgba(255, 255, 255, 0.45)
    ),
    url('/img/grey-asoggetti.jpg') no-repeat center center fixed;
    background-size: cover;
}




